export default "<cn>\r\n#### 自定义头部\r\n自定义日历头部内容。\r\n</cn>\r\n\r\n<us>\r\n#### Customize Header\r\nCustomize Calendar header content.\r\n</us>\r\n\r\n<template>\r\n  <div style=\"width: 300px; border: 1px solid #d9d9d9; border-radius: 4px\">\r\n    <a-calendar :fullscreen=\"false\" :header-render=\"headerRender\" @panelChange=\"onPanelChange\" />\r\n  </div>\r\n</template>\r\n<script>\r\nexport default {\r\n  methods: {\r\n    onPanelChange(value, mode) {\r\n      // eslint-disable-next-line no-console\r\n      console.log(value, mode);\r\n    },\r\n    headerRender({ value, type, onChange, onTypeChange }) {\r\n      const start = 0;\r\n      const end = 12;\r\n      const monthOptions = [];\r\n\r\n      const current = value.clone();\r\n      const localeData = value.localeData();\r\n      const months = [];\r\n      for (let i = 0; i < 12; i++) {\r\n        current.month(i);\r\n        months.push(localeData.monthsShort(current));\r\n      }\r\n\r\n      for (let index = start; index < end; index++) {\r\n        monthOptions.push(\r\n          <a-select-option class=\"month-item\" key={`${index}`}>\r\n            {months[index]}\r\n          </a-select-option>,\r\n        );\r\n      }\r\n      const month = value.month();\r\n\r\n      const year = value.year();\r\n      const options = [];\r\n      for (let i = year - 10; i < year + 10; i += 1) {\r\n        options.push(\r\n          <a-select-option key={i} value={i} class=\"year-item\">\r\n            {i}\r\n          </a-select-option>,\r\n        );\r\n      }\r\n      return (\r\n        <div style={{ padding: '10px' }}>\r\n          <div style={{ marginBottom: '10px' }}>Custom header </div>\r\n          <a-row type=\"flex\" justify=\"space-between\">\r\n            <a-col>\r\n              <a-radio-group size=\"small\" onChange={e => onTypeChange(e.target.value)} value={type}>\r\n                <a-radio-button value=\"month\">Month</a-radio-button>\r\n                <a-radio-button value=\"year\">Year</a-radio-button>\r\n              </a-radio-group>\r\n            </a-col>\r\n            <a-col>\r\n              <a-select\r\n                size=\"small\"\r\n                dropdownMatchSelectWidth={false}\r\n                class=\"my-year-select\"\r\n                onChange={newYear => {\r\n                  const now = value.clone().year(newYear);\r\n                  onChange(now);\r\n                }}\r\n                value={String(year)}\r\n              >\r\n                {options}\r\n              </a-select>\r\n            </a-col>\r\n            <a-col>\r\n              <a-select\r\n                size=\"small\"\r\n                dropdownMatchSelectWidth={false}\r\n                value={String(month)}\r\n                onChange={selectedMonth => {\r\n                  const newValue = value.clone();\r\n                  newValue.month(parseInt(selectedMonth, 10));\r\n                  onChange(newValue);\r\n                }}\r\n              >\r\n                {monthOptions}\r\n              </a-select>\r\n            </a-col>\r\n          </a-row>\r\n        </div>\r\n      );\r\n    },\r\n  },\r\n};\r\n</script>\r\n";